import { Accordion, AccordionDetails, AccordionSummary, Box, Card, Stack, Typography, useTheme } from '@mui/material';
import { Iconify } from '@riptech/minimal-ui/src/components/iconify';
import { useBoolean } from '@riptech/minimal-ui/src/hooks/use-boolean';
import { useCopyToClipboard } from '@riptech/minimal-ui/src/hooks/use-copy-to-clipboard';
import { useAppContext } from '@riptech/shared-ui/src/hooks/useAppContext';
import Image from 'next/image';
import type { FC } from 'react';
import AppLogo from '../../../public/assets/logos/su-icon.svg';
import CopyButton from '../common/CopyButton';
import ASINListTable from './ASINListTable';
import ASINListUtils from './ASINListUtils';
import Categories from './Categories';
export interface ASINListData {
  currentAsin: string;
  dimensionColumns: Array<string>;
  dimensionValues: Record<string, Array<string>>;
  parentAsin: string;
}
export interface Category {
  rank: string;
  name: string;
}
const ASINList: FC = () => {
  const {
    appName
  } = useAppContext();
  const theme = useTheme();
  const showTable = useBoolean();
  const {
    copy
  } = useCopyToClipboard();
  const asinData = ASINListUtils.extractAsinData();
  const categories = ASINListUtils.extractCategories();
  if (!asinData && !categories) {
    return null;
  }
  const onCopy = (e: React.MouseEvent<HTMLElement>, text: string) => {
    e.stopPropagation();
    copy(text);
  };
  const onCopyTable = (e: React.MouseEvent<HTMLElement>) => {
    if (asinData) {
      onCopy(e, ASINListUtils.generateCSVText(asinData.dimensionValues, asinData.dimensionColumns));
    }
  };
  return <Card sx={{
    p: 2
  }} data-sentry-element="Card" data-sentry-component="ASINList" data-sentry-source-file="ASINList.tsx">
      <Stack alignItems="center" direction="row" spacing={2} data-sentry-element="Stack" data-sentry-source-file="ASINList.tsx">
        <Image alt={appName ?? ''} {...AppLogo} style={{
        height: theme.spacing(5),
        marginRight: 40,
        objectFit: 'contain',
        width: theme.spacing(5)
      }} data-sentry-element="Image" data-sentry-source-file="ASINList.tsx" />
        <Stack width="100%" data-sentry-element="Stack" data-sentry-source-file="ASINList.tsx">
          {asinData ? <Box alignItems="center" display="flex" justifyContent="space-between" width="100%">
              <Typography variant="body2">
                Selected ASIN: <b>{asinData?.currentAsin}</b>
              </Typography>
              <Stack alignItems="center" direction="row" spacing={0.5}>
                <Typography variant="body2">
                  Parent ASIN: <b>{asinData?.parentAsin}</b>
                </Typography>
                <CopyButton onCopy={e => onCopy(e, asinData.parentAsin)} />
              </Stack>
            </Box> : <Typography variant="body2">
              <b>No Children</b>
            </Typography>}
          {categories ? <Categories data={categories} /> : null}
        </Stack>
      </Stack>
      {asinData ? <Accordion expanded={showTable.value} sx={{
      mb: -2,
      mt: 2
    }}>
          <AccordionSummary expandIcon={<Iconify icon="eva:chevron-up-fill" />} onClick={showTable.onToggle}>
            <Stack alignItems="center" direction="row" spacing={0.5}>
              <Typography variant="body2">
                {showTable.value ? 'Hide' : 'Show'} Child Table ({Object.keys(asinData.dimensionValues).length}{' '}
                Variations)
              </Typography>
              <CopyButton onCopy={onCopyTable} />
            </Stack>
          </AccordionSummary>
          <AccordionDetails sx={{
        boxShadow: 'none'
      }}>
            <Box display="flex" justifyContent="space-between" sx={{
          mt: '20px'
        }}>
              <ASINListTable columns={asinData.dimensionColumns} data={asinData.dimensionValues} />
            </Box>
          </AccordionDetails>
        </Accordion> : null}
    </Card>;
};
export default ASINList;