import { IconButton, Tooltip } from '@mui/material';
import { Iconify } from '@riptech/minimal-ui/src/components/iconify';
import { enqueueSnackbar } from '@riptech/minimal-ui/src/components/snackbar';
import type { FC } from 'react';
import React from 'react';
interface CopyButtonProps {
  readonly onCopy: (e: React.MouseEvent<HTMLElement>) => void;
}
const CopyButton: FC<CopyButtonProps> = ({
  onCopy
}) => {
  const onClick = (e: React.MouseEvent<HTMLElement>) => {
    onCopy(e);
    enqueueSnackbar('Copied data to clipboard', {
      variant: 'info'
    });
  };
  return <Tooltip title="Copy" data-sentry-element="Tooltip" data-sentry-component="CopyButton" data-sentry-source-file="CopyButton.tsx">
      <IconButton onClick={onClick} data-sentry-element="IconButton" data-sentry-source-file="CopyButton.tsx">
        <Iconify icon="eva:copy-fill" data-sentry-element="Iconify" data-sentry-source-file="CopyButton.tsx" />
      </IconButton>
    </Tooltip>;
};
export default CopyButton;