import type { ReactElement } from 'react';
import React from 'react';
import ASINList from '../../components/asinList/ASINList';
import useFetchUserSettings from '../../hooks/useFetchUserSettings';
import type { ComponentWithLayout } from '../../hooks/useLayout';
const ASINListPage: ComponentWithLayout = () => {
  const {
    data
  } = useFetchUserSettings();
  if (data?.userData?.settings?.uiEnhancements?.asinList) {
    return <ASINList />;
  }
  return null;
};
ASINListPage.getLayout = (page: ReactElement) => page;
export default ASINListPage;