import { useQuery } from '@tanstack/react-query';

import RequestProxy, { FETCH_USER_DATA_QUERY_KEY } from '../utils/RequestProxy';

export default function useFetchUserSettings() {
  return useQuery({
    queryFn: RequestProxy.fetchUserData,
    queryKey: [FETCH_USER_DATA_QUERY_KEY],
    meta: {
      errorMessage: 'Failed to load Seller Utilities user data'
    }
  });
}
