import type { ASINListData, Category } from './ASINList';

export default class ASINListUtils {
  static extractAsinData = (): ASINListData | null => {
    const dimensionValuesPattern = /"dimensionValuesDisplayData"\s*:\s*({[^}]*})/;
    const currentAsinPattern = /"currentAsin"\s*:\s*"([^"]+)"/;
    const parentAsinPattern = /"parentAsin"\s*:\s*"([^"]+)"/;
    const dimensionsPattern = /"dimensions"\s:\s*\[([^\]]*)]/;

    const result: ASINListData = {
      currentAsin: '',
      dimensionColumns: ['one', 'two', 'three'],
      dimensionValues: {},
      parentAsin: ''
    };

    const scripts = document.querySelectorAll('#centerCol script[type="text/javascript"]');
    const scriptContent = Array.from(scripts).find(
      (script) =>
        script?.textContent?.match(dimensionValuesPattern) &&
        script?.textContent?.match(currentAsinPattern) &&
        script?.textContent?.match(parentAsinPattern) &&
        script?.textContent?.match(dimensionsPattern)
    )?.textContent;

    if (!scriptContent) {
      return null;
    }

    const dimensionValuesMatch = scriptContent.match(dimensionValuesPattern);
    const currentAsinMatch = scriptContent.match(currentAsinPattern);
    const parentAsinMatch = scriptContent.match(parentAsinPattern);
    const dimensionsMatch = scriptContent.match(dimensionsPattern);

    if (dimensionsMatch) {
      const dimensionsContent = dimensionsMatch[1];
      const dynamicColumns = JSON.parse(`[${dimensionsContent}]`).map((str: string) =>
        str
          .split('_')
          .map((part) => part.charAt(0).toUpperCase() + part.slice(1))
          .join(' ')
      );
      result.dimensionColumns = ['ASIN', ...dynamicColumns];
    }

    return {
      ...result,
      currentAsin: currentAsinMatch ? currentAsinMatch[1] : '',
      dimensionValues: dimensionValuesMatch ? JSON.parse(dimensionValuesMatch[1]) : {},
      parentAsin: parentAsinMatch ? parentAsinMatch[1] : ''
    };
  };

  static extractCategories = (): Array<Category> | null => {
    const startsWithRankingNumberRegex = /^#\d{1,3}(,\d{3})*\b/;
    const rankingNumberRegex = /#\d{1,3}(,\d{3})*\b/;

    // categories can be found in two types of elements
    const productDetailsTableRows = document.querySelectorAll('#productDetails_detailBullets_sections1 tr td');
    const productDetailsList = document.querySelectorAll(
      '#detailBulletsWrapper_feature_div .detail-bullet-list .a-list-item'
    );

    const td = Array.from(productDetailsTableRows).find((row) =>
      row.textContent?.trim().match(startsWithRankingNumberRegex)
    );
    const productListWithRankings = Array.from(productDetailsList).filter((item) =>
      (item as HTMLElement)?.innerText?.trim()?.match(rankingNumberRegex)
    );

    if (!td && !productListWithRankings.length) {
      return null;
    }

    if (td) {
      const rankingListSpans = Array.from(td?.querySelectorAll('span > span'));
      return this.extractCategoriesFromList(rankingListSpans);
    }

    return this.extractCategoriesFromList(productListWithRankings);
  };

  private static readonly extractCategoriesFromList = (list: Array<Element>): Array<Category> =>
    list
      .map((span) => {
        const text = (span as HTMLElement).innerText;
        const regex = /#(\d{1,3}(,\d{3})*)\s+in\s+(.*)/;

        return regex.exec(text);
      })
      .filter((val) => !!val)
      .map((regexMatch, index) => {
        let categoryName = regexMatch[3];
        if (index === 0) {
          categoryName = categoryName.substring(0, regexMatch[3].indexOf('(') - 1);
        }

        return { name: categoryName, rank: regexMatch[1] };
      });

  static generateCSVText = (data: ASINListData['dimensionValues'], columns: Array<string>) =>
    [columns.join('\t'), ...Object.entries(data).map(([asinKey, asinData]) => [asinKey, ...asinData].join('\t'))].join(
      '\n'
    );
}
